
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, dl, dt, dd, ol, nav ul, nav li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin : 0; padding : 0; border : 0; font-size : 100%; font : inherit; vertical-align : baseline; }

article, aside, details, figcaption, figure, footer, header, menu, nav, section { display : block; }

ol, ul { list-style : none; margin : 0px; padding : 0px; }

blockquote, q { quotes : none; }

blockquote:before, blockquote:after, q:before, q:after { content : ''; content : none; }

table { border-collapse : collapse; border-spacing : 0; }

a { text-decoration : none; }

.txt-rt { text-align : right; }

.txt-lt { text-align : left; }

.txt-center { text-align : center; }

.float-rt { float : right; }

.float-lt { float : left; }

.clear { clear : both; }

.pos-relative { position : relative; }

.pos-absolute { position : absolute; }

.vertical-base { vertical-align : baseline; }

.vertical-top { vertical-align : top; }

nav.vertical ul li { display : block; }

nav.horizontal ul li { display : inline-block; }

img { max-width : 100%; }

body {
    background  : #D4202B;
    font-family : 'Open Sans', sans-serif;
}

.login-form {
    background            : #EEE;
    width                 : 26%;
    position              : relative;
    -webkit-border-radius : 0.4em;
    -o-border-radius      : 0.4em;
    -moz-border-radius    : 0.4em;
    top                   : 0; right : 0; left : 0; bottom : 0;
    margin-left           : auto;
    margin-right          : auto;
}

.head {
    position : absolute;
    top      : -15%;
    width    : 100%;
}

#logo {
    width : 120px;
}

.head img {
    border-radius         : 50%;
    -webkit-border-radius : 50%;
    -o-border-radius      : 50%;
    -moz-border-radius    : 50%;
    border                : 6px solid rgba(221, 218, 215, 0.23);
}

.main {
    position : relative;
}

.main h1 {
    font-size   : 25px;
    color       : #676767;
    font-family : 'Open Sans', sans-serif;
    font-weight : 400;
    padding-top : 19%;
    text-align  : center;
}

.main form {
    width   : 80%;
    margin  : 0 auto;
    padding : 6% 0 9% 0;
}

.main p {
    text-align : center;
}

.main form p a {
    color       : #888;
    font-family : 'Open Sans', sans-serif;
}

form p a:hover {
    color : #3A3A3C;
}

input[type="text"], input[type="password"] {
    text-align         : left;
    position           : relative;
    width              : 92%;
    padding            : 3%;
    background         : #D3D3D3;
    margin-top         : 6%;
    font-family        : 'Open Sans', sans-serif;
    color              : #676767;
    font-weight        : 600;
    font-size          : 16px;
    outline            : none;
    border-radius      : 5px;
    border             : 1px solid #DED6D6;
    -webkit-appearance : none;
}

input[type="text"]:hover, input[type="password"]:hover {
    border             : 1px solid #949494;
    transition         : 0.5s;
    -webkit-transition : 0.5s;
    -moz-transition    : 0.5s;
    -o-transition      : 0.5s;
    -ms-transition     : 0.5s;
}

input[type="submit"] {
    width               : 99%;
    padding             : 3%;
    margin-bottom       : 8%;
    margin-top          : 6%;
    background          : #3A3A3C;
    font-family         : 'Open Sans', sans-serif;
    color               : #ECECEC;
    box-shadow          : inset 0px 0px 10px #666464;
    -webkit-text-shadow : 0px 0px 3px #000;
    -moz-text-shadow    : 0px 0px 3px #000;
    -o-text-shadow      : 0px 0px 3px #000;
    -ms-text-shadow     : 0px 0px 3px #000;
    font-size           : 20px;
    outline             : none;
    border              : none;
    cursor              : pointer;
    font-weight         : 500;
    border-radius       : 5px;
    transition          : 0.5s;
    -webkit-appearance  : none;
    -webkit-transition  : 0.5s;
    -moz-transition     : 0.5s;
    -o-transition       : 0.5s;
    -ms-transition      : 0.5s;
}

input[type="submit"]:hover {
    background : #3A3A3C;
    color      : #FFF;
}

.copy-right {
    position : absolute;
    bottom   : -19%;
    left     : 43.7%;
}

.copy-right p {
    color       : #FFF;
    font-size   : 1em;
    font-family : 'Open Sans', sans-serif;
    font-weight : 600;
}

.copy-right p a {
    font-family        : 'Open Sans', sans-serif;
    font-size          : 1em;
    color              : #3A3A3C;
    -webkit-transition : all 0.3s ease-out;
    -moz-transition    : all 0.3s ease-out;
    -ms-transition     : all 0.3s ease-out;
    -o-transition      : all 0.3s ease-out;
    transition         : all 0.3s ease-out;
}

.copy-right p a:hover {
    color : #FFF;
}

@media (max-width : 1440px) {
    .login-form {
        width : 30%;
    }
    .main h1 {
        padding-top : 23%;
    }
    .copy-right {
        bottom : -26%;
    }
}

@media (max-width : 1366px) {
    .login-form {
        width : 32%;
    }
}

@media (max-width : 1280px) {
    .login-form {
        width : 34%;
    }
    .copy-right {
        left   : 41%;
        bottom : -18%;
    }
}

@media (max-width : 1024px) {
    .login-form {
        width : 45%;
    }
    .copy-right {
        left   : 41%;
        bottom : -18%;
    }
}

@media (max-width : 768px) {
    .login-form {
        width : 59%;
    }
    .copy-right {
        left   : 38%;
        bottom : -14%;
    }
}

@media (max-width : 640px) {
    .login-form {
        width : 63%;
    }
    .copy-right {
        left   : 36%;
        bottom : -18%;
    }
}

@media (max-width : 480px) {
    .login-form {
        width : 74%;
    }
    .copy-right {
        left   : 30%;
        bottom : -17%;
    }
    .main h1 {
        font-size : 22px;
    }
    .head img {
        width : 78%;
    }
    .head {
        top  : -15%;
        left : 34%;
    }
    input[type="text"], input[type="password"], input[type="submit"] {
        font-weight   : 600;
        margin-bottom : 4%;
    }
}

@media (max-width : 320px) {
    .login-form {
        width : 85%;
    }
    .main h1 {
        padding-top : 20%;
        font-size   : 20px;
    }
    .head img {
        width  : 60%;
        border : 5px solid rgba(221, 218, 215, 0.23);
    }
    .head {
        top  : -15%;
        left : 36%;
    }
    input[type="text"], input[type="password"], input[type="submit"] {
        font-weight : 600;
        font-size   : 15px;
    }
    .main form p a {
        font-size : 15px;
    }
    input[type="submit"] {
        padding : 4%;
    }
}

.outer {
    display  : table;
    position : absolute;
    height   : 100%;
    width    : 100%;
}

.middle {
    display        : table-cell;
    vertical-align : middle;
}

.inner {
    margin-left  : auto;
    margin-right : auto;
    width        : 100%;
    text-align   : center;
}